import React, { useState } from 'react'
import style from './index.module.scss'
import { useLocation, useNavigate } from 'react-router-dom'
import { Button, Menu } from 'antd'
import classNames from 'classnames'
import { LeftOutlined, MenuFoldOutlined, MenuUnfoldOutlined, RightOutlined } from '@ant-design/icons'
import { IMenuConfig, IMenuItem } from './type'

interface IProps extends React.ComponentProps<any> {
  brand?: {
    content?: React.ReactNode
    collapsedContent?: React.ReactNode
    onClick?: () => void
  }
  mode?: 'siderLeft' | 'siderMiddleLeft' | 'siderMiddleRight'
  height?: string
  sider?: React.ReactNode
  header?: React.ReactNode
  footer?: React.ReactNode
  content?: React.ReactNode
  menu?: IMenuConfig
}

const Layout = ({
  brand,
  mode = 'siderLeft',
  height = '100%',
  content = <div className={style.defaultContent}>content</div>,
  header = <div className={style.defaultHeader}>header</div>,
  footer = <div className={style.defaultFooter}>footer</div>,
  sider = <div className={style.defaultSider}>sider</div>,
  menu,
  ...rest
}: IProps) => {
  const { collapsedTrigger, dark = false, items, defaultCollapsedItems = [], onItemClick, collapsed = false, ...menuRest } = menu || {}
  const { content: brandContent = 'Brand', collapsedContent: brandCollapsedContent = 'B', onClick: onBrandClick = () => console.log('brand click') } = brand || {}

  const navigate = useNavigate()
  const location = useLocation()

  const [menuCollapsed, setMenuCollapsed] = useState<boolean>(collapsed)

  const generateMenu = () => {
    const findInCollapsedPaths = (items: IMenuItem[] | undefined) => {
      if (!items || items.length === 0) return []
      const paths: string[] = []
      items.forEach((item) => {
        if (item.children && item.children.length > 0 && !defaultCollapsedItems.includes(item.path)) {
          paths.push(item.path)
          const subPaths = findInCollapsedPaths(item.children)
          if (subPaths && subPaths.length > 0) {
            paths.push(...subPaths)
          }
        }
      })
      return paths
    }

    const inCollapsedPaths = findInCollapsedPaths(items)

    const generateItems: any = (items: IMenuItem[] | undefined) => {
      if (!items || items.length === 0) return null
      return (
        items?.map((item) => ({
          icon: item.icon,
          key: item.path,
          label: item.label,
          disabled: item.disabled,
          children: generateItems(item.children),
        })) || []
      )
    }

    const finalItems = generateItems(items)

    const onMenuItemClick = ({ keyPath }: any) => {
      const path = ('/' + keyPath.reverse().join('/')).replace('//', '/')
      // 
      onItemClick ? onItemClick(path) : navigate(path)
    }

    return (
      <Menu
        style={{ overflow: 'auto', flex: 1 }}
        className={classNames(style.antdMenu, {
          [style.antdMenuCollapsed]: menuCollapsed,
        })}
        theme={dark ? 'dark' : 'light'}
        onClick={onMenuItemClick}
        selectedKeys={location?.pathname.split('/').reverse()}
        defaultOpenKeys={inCollapsedPaths || []}
        inlineCollapsed={menuCollapsed}
        items={finalItems}
        mode="inline"
        {...menuRest}
      />
    )
  }

  return (() => {
    switch (mode) {
      case 'siderLeft':
        return (
          <div className={style.layoutSiderLeft} style={{ height: height }} {...rest}>
            {menu ? (
              <div
                className={classNames(style.menu, {
                  [style.menuDark]: menu && dark,
                  [style.menuCollapsed]: menuCollapsed,
                })}>
                {brand && (
                  <div
                    className={classNames(style.brandWrap, {
                      [style.brandWrapDark]: dark,
                      [style.brandWrapCollapsed]: menuCollapsed,
                    })}>
                    <div
                      onClick={onBrandClick}
                      className={classNames(style.brand, {
                        [style.brandDark]: dark,
                      })}>
                      {menuCollapsed ? brandCollapsedContent : brandContent}
                    </div>
                  </div>
                )}

                {generateMenu()}
                {(collapsedTrigger === 'siderBottom' || collapsedTrigger === 'both') && (
                  <div
                    className={classNames(style.collapsed, {
                      [style.collapsedDark]: dark,
                      [style.collapsedCollapsed]: menuCollapsed,
                    })}
                    onClick={() => setMenuCollapsed(!menuCollapsed)}>
                    {menuCollapsed ? <RightOutlined /> : <LeftOutlined />}
                  </div>
                )}
              </div>
            ) : (
              <div className={style.SLSider}>{sider}</div>
            )}
            <div className={style.SLMain}>
              <div className={style.SLHeaderWrap}>
                {(collapsedTrigger === 'headerLeft' || collapsedTrigger === 'both') && (
                  <Button className={style.collapsedButton} onClick={() => setMenuCollapsed(!menuCollapsed)}>
                    {menuCollapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
                  </Button>
                )}
                <div className={style.SLHeader}>{header}</div>
              </div>
              <div className={style.SLWrapper}>
                <div className={style.SLContent}>{content}</div>
                <div className={style.SLFooter}>{footer}</div>
              </div>
            </div>
          </div>
        )
      case 'siderMiddleLeft':
        return (
          <div className={style.layoutSiderMiddleLeft} style={{ height: height }} {...rest}>
            <div className={style.SMHeader}>{header}</div>
            <div className={style.SMMain}>
              {menu ? (
                <div
                  className={classNames(style.menu, {
                    [style.menuDark]: menu && dark,
                    [style.menuCollapsed]: menuCollapsed,
                  })}>
                  {generateMenu()}
                  {(collapsedTrigger === 'siderBottom' || collapsedTrigger === 'both') && (
                    <div
                      className={classNames(style.collapsed, {
                        [style.collapsedDark]: dark,
                        [style.collapsedCollapsed]: menuCollapsed,
                      })}
                      onClick={() => setMenuCollapsed(!menuCollapsed)}>
                      {menuCollapsed ? <RightOutlined /> : <LeftOutlined />}
                    </div>
                  )}
                </div>
              ) : (
                <div className={style.SMSider}>{sider}</div>
              )}
              <div className={style.SMWrapper}>
                <div className={style.SMContent}>{content}</div>
                <div className={style.SMFooter}>{footer}</div>
              </div>
            </div>
          </div>
        )
      case 'siderMiddleRight':
        return (
          <div className={style.layoutSiderMiddleRight} style={{ height: height }} {...rest}>
            <div className={style.SMHeader}>{header}</div>
            <div className={style.SMMain}>
              <div className={style.SMWrapper}>
                <div className={style.SMContent}>{content}</div>
                <div className={style.SMFooter}>{footer}</div>
              </div>
              {menu ? (
                <div
                  className={classNames(style.menu, {
                    [style.menuDark]: menu && dark,
                    [style.menuCollapsed]: menuCollapsed,
                  })}>
                  {generateMenu()}
                  {(collapsedTrigger === 'siderBottom' || collapsedTrigger === 'both') && (
                    <div
                      className={classNames(style.collapsed, {
                        [style.collapsedDark]: dark,
                        [style.collapsedCollapsed]: menuCollapsed,
                      })}
                      onClick={() => setMenuCollapsed(!menuCollapsed)}>
                      {!menuCollapsed ? <RightOutlined /> : <LeftOutlined />}
                    </div>
                  )}
                </div>
              ) : (
                <div className={style.SMSider}>{sider}</div>
              )}
            </div>
          </div>
        )
    }
  })()
}

export default Layout
