import React from 'react'
import AnimateCss, { AnimateCssProps } from './children/animate_css'
import AnimateCollapse, { AnimateCollapseProps } from './children/collapse'
import AnimateFade, { AnimateFadeProps } from './children/fade'
import classNames from 'classnames'

/**
 * Animate  dom  children
 * @param children 
 * @param inClassName  className
 * @param className  className,  children  className
 * @param outClassName  className
 * @param delay 
 * @param duration 
 * @param visible 
 * @param loop 
 * @param unmountOnExit 
 */
export interface AnimateProps extends React.ComponentProps<any> {
  delay?: number
  duration?: number
  visible?: boolean
  loop?: boolean
  children: React.ReactElement
  unmountOnExit?: boolean
  inClassName?: string
  outClassName?: string
  className?: string
}

interface AnimateChildren {
  AnimateCss: React.FC<AnimateCssProps>
  AnimateCollapse: React.FC<AnimateCollapseProps>
  AnimateFade: React.FC<AnimateFadeProps>
}

const Animate: React.FC<AnimateProps> & AnimateChildren = ({ children, style, inClassName, outClassName, className, delay, duration, visible = true, loop, unmountOnExit }: AnimateCssProps) => {
  const [unmount, setUnmount] = React.useState<boolean>(false)

  return unmountOnExit && unmount ? (
    <></>
  ) : (
    React.cloneElement(children, {
      onAnimationEnd: () => {
        setUnmount(true)
      },
      ...children?.props,
      className: classNames(
        className,
        {
          [inClassName]: inClassName && visible,
          [outClassName]: outClassName && !visible,
        },
        children?.props?.className,
      ),
      children: children?.props?.children,
      style: {
        ...{
          animationDelay: `${delay}ms`,
          animationDuration: `${duration}ms`,
          pointerEvents: visible ? 'all' : 'none',
          animationIterationCount: loop ? 'infinite' : 1,
        },
        ...style,
        ...children?.props?.style,
      },
    })
  )
}

Animate.AnimateCss = AnimateCss
Animate.AnimateCollapse = AnimateCollapse
Animate.AnimateFade = AnimateFade
export default Animate
