import Animate, { AnimateProps } from '../../index'
import React from 'react'
import style from './index.module.scss'

export interface AnimateFadeProps extends Omit<AnimateProps, 'inClassName' | 'outClassName'> {
  children: React.ReactElement
}

const AnimateFade = ({ children, ...rest }: AnimateFadeProps) => {
  return (
    <Animate {...rest} className={style.fade} inClassName={style.fadeIn} outClassName={style.fadeOut}>
      {children}
    </Animate>
  )
}
export default AnimateFade
