import React, { useEffect, useState } from 'react'
import style from './index.module.scss'

interface TypewriterProps extends React.ComponentProps<any> {
  text: string
  /**
   * @description 
   * @default 100
   */
  speed?: number
}

const Typewriter = ({ text = '', speed = 50, className }: TypewriterProps) => {
  const [displayedText, setDisplayedText] = useState('')
  const [isTyping, setIsTyping] = useState(true)

  useEffect(() => {
    if (isTyping && displayedText.length < text.length) {
      setTimeout(() => {
        setDisplayedText(text.substring(0, displayedText.length + 1))
      }, speed)
    } else if (displayedText.length === text.length) {
      setIsTyping(false)
    }
  }, [displayedText, isTyping])

  useEffect(() => {
    setDisplayedText('')
  }, [speed, text])

  return (
    <div className={className}>
      {displayedText}
      {!isTyping && <span className={style.blinkingCursor}> _</span>}
    </div>
  )
}

export default Typewriter
