import { CreateAxiosDefaults } from 'axios'
import qs from 'qs'

export const API_CODE_ERROR = 5000
export const API_CODE_UNEXPECT = 7000
export const API_STATUS_OK = 200
export const API_STATUS_ERROR = 500

export const API_DEFAULT_CONFIG: CreateAxiosDefaults = {
  // `url`  URL
  url: '',
  // `baseURL`  `url`  `url`  URL
  //  `baseURL`  axios  URL
  baseURL: '',
  // `paramsSerializer`  `params` 
  // (e.g. https://www.npmjs.com/package/qs, http://api.jquery.com/jquery.param/)
  paramsSerializer: (params: Record<string, any>) => {
    return qs.stringify(params, { arrayFormat: 'comma' })
  },
  // `timeout` (0 )
  //  `timeout` 
  timeout: 5000,
  // `withCredentials` 
  withCredentials: false, // 
  // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
  responseType: 'json', // 
  // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
  validateStatus: (status: number) => {
    return status >= 200 && status < 300 // 
  },
}
