import cookie from 'react-cookies'

type TokenType = 'cookie' | 'localStorage'

const usingToken: (type?: TokenType) => [token: string, setToken: (token: string) => void, clearToken: () => void] = (type = 'localStorage') => {
  const KEY = 'token'

  const token = (type === 'cookie' ? cookie.load(KEY) : localStorage.getItem(KEY)) || undefined

  const setToken = (newToken: string) => {
    type === 'cookie'
      ? cookie.save(KEY, newToken, {
          path: '/',
          maxAge: 60 * 60 * 24 * 365 * 999,
        })
      : localStorage.setItem(KEY, newToken)
  }

  const clearToken = () => {
    type === 'cookie' ? cookie.remove(KEY, { path: '/' }) : localStorage.removeItem(KEY)
  }

  return [token, setToken, clearToken]
}

export default usingToken
