import Animate, { AnimateProps } from '../../index'
import React, { useEffect, useRef } from 'react'
import style from './index.module.scss'

export interface AnimateCollapseProps extends Omit<AnimateProps, 'inClassName' | 'outClassName'> {
  children: React.ReactElement
}

const AnimateCollapse = ({ children, visible, ...rest }: AnimateCollapseProps) => {
  const childRef = useRef<HTMLElement | null>(null)
  const [height, setHeight] = React.useState<number>()

  useEffect(() => {
    setHeight(childRef.current?.clientHeight)
  }, [])

  return (
    <Animate {...rest} visible={visible} style={{ height: visible ? height : 0 }} className={style.collapse}>
      {React.cloneElement(children, {
        ...children?.props,
        ref: (element: any) => {
          childRef.current = element
          if (children.props.ref) {
            children.props.ref(element)
          }
        },
        style: {
          ...children?.props?.style,
          ...(!visible ? { height: 0 } : {}),
        },
      })}
    </Animate>
  )
}
export default AnimateCollapse
