/**
 * AnimateCss  dom  children
 * animate.css https://animate.style
 * @param children 
 * @param inName  animate.css 
 * @param className  className,  children  className
 * @param outName  animate.css 
 * @param delay 
 * @param duration 
 * @param visible 
 * @param loop 
 * @param unmountOnExit 
 */
export interface AnimateCssProps extends Omit<AnimateProps, 'inClassName' | 'outClassName' | 'bothClassName'> {
  inName?: AnimateCssType | string
  outName?: AnimateCssType | string
  children: React.ReactElement
}

const AnimateCss = ({ children, inName, className, outName, delay = 0, duration, visible = true, loop, unmountOnExit }: AnimateCssProps) => {
  const [unmount, setUnmount] = React.useState<boolean>(false)

  return unmountOnExit && unmount ? (
    <></>
  ) : (
    React.cloneElement(children, {
      onAnimationEnd: () => {
        setUnmount(true)
      },
      ...children?.props,
      className: classNames('animate__animated', 'animate__fadeIn', `animate__${visible ? inName : outName}`, className, children?.props?.className),
      children: children?.props?.children,
      style: {
        ...{
          animationDelay: `${delay}ms`,
          animationDuration: `${duration}ms`,
          pointerEvents: visible ? 'all' : 'none',
          animationIterationCount: loop ? 'infinite' : 1,
        },
        ...children?.props?.style,
      },
    })
  )
}

export default AnimateCss
